import React, { Component } from 'react'
import { NavBar, Icon, Tabs,Carousel,WingBlank, WhiteSpace,
    Grid,Flex} from 'antd-mobile';
export default class AppHome extends Component {
    render() {
        return (
            <div>
                <NavBar
                    style={{backgroundColor:'#33cccc',color:'#fff'}}>
                        住吧家居
                </NavBar>
                <div style={{ height: '200px' }}>
                    <Carousel
                        autoplay={true}
                        infinite
                        >
                         {[1,2].map(val => (
                        <a
                            key={val}
                            href="http://www.alipay.com"
                            style={{ display: 'inline-block', width: '100%', height: 200 }}
                            >
                            <img
                                src='./images/big.png'
                                alt=""
                                style={{ width: '100%', verticalAlign: 'top' }}
                                onLoad={() => {
                                window.dispatchEvent(new Event('resize'));
                                this.setState({ imgHeight: 'auto' });
                                }}
                            />
                        </a>
                        ))}
                    </Carousel>
                </div>
                <div className="flex-container">
                    <WhiteSpace size="lg" />
                    <WingBlank size="md">
                        <div className="flex-container">
                            <Flex wrap="wrap">
                            <img src='./images/bo.png' className="inline" />
                            <img src='./images/bo.png' className="inline"/>
                            <img src='./images/bo.png' className="inline"/>
                            </Flex>
                        </div>
                    </WingBlank>
                </div>
                <div>
                    <div style={{background:"#fff"}}>
                        <div>
                            <Flex>
                                <Flex.Item style={{fontSize:20}}>热门推荐</Flex.Item>
                            </Flex>
                            <div className="bott">
                                <Flex wrap="wrap">
                                    <Flex.Item>
                                        <img src='./images/bott.png'/>
                                    </Flex.Item>
                                </Flex>
                            </div>
                        </div>
                        
                    </div>
                </div>
            </div>
        )
    }
}